<template>
  <div class="deposit-money block">

    <!-- Loading Overlay -->
    <loading :active.sync="isLoading"
             :is-full-page="false"
             color="#0060f1"
             loader="dots">
    </loading>

    <div class="block__title">Deposit money</div>
    <div class="block__titleLine"></div>

    <!-- iframe-->
    <iframe height="400" :src="cashierUrl" v-if="cashierUrl" allowfullscreen></iframe>

    <!-- Form   -->
    <form class="registration__form" @submit.prevent="deposit" v-else>

      <div class="block__row checkBoxRow">

        <!-- Radio buttons -->
        <div class="checkbox">
          <input class="checkbox__input" id="deposit1" value="50" v-model="amount" type="radio">
          <label class="checkbox__label" for="deposit1">
            <span class="checkbox__text">{{currency}} 50</span>
          </label>
        </div>

        <div class="checkbox">
          <input class="checkbox__input" id="deposit2" value="100" v-model="amount" type="radio">
          <label class="checkbox__label" for="deposit2">
            <span class="checkbox__text">{{currency}} 100</span>
          </label>
        </div>

        <div class="checkbox">
          <input class="checkbox__input" id="deposit3" value="200" v-model="amount" type="radio">
          <label class="checkbox__label" for="deposit3">
            <span class="checkbox__text">{{currency}} 200</span>
          </label>
        </div>

        <div class="checkbox">
          <input class="checkbox__input" id="deposit4" value="500" v-model="amount" type="radio">
          <label class="checkbox__label" for="deposit4">
            <span class="checkbox__text">{{currency}} 500</span>
          </label>
        </div>

      </div>

      <div data-v-182ac4db="" class="social-auth__footer">
        OR
      </div>

      <div class="block__row nameRow">

        <v-input :name="`Amount (${currency})`"
                 type="text"
                 v-model="amount"
                 @change="updateAmount"
                 :isError="$v.amount.$error">
          <template slot="error">
            <div class="error" v-if="!$v.amount.required">Amount is required</div>
          </template>
        </v-input>
      </div>

      <div class="block__row nameRow">
        <!-- Bonus -->
        <v-input name="Bonus code"
                 type="text"
                 v-model="bonusCode"
                 @change="updateBonusCode">
        </v-input>

      </div>

      <!-- btn -->
      <button class="btn " type="submit" name="Create an account">Continue</button>
    </form>

  </div>
</template>

<script>
  import Input from '@/components/Input';
  import call from '@/services/callService';
  import subscribe from "@/services/subscribeService";
  import {mapGetters} from 'vuex';
  import {required} from 'vuelidate/lib/validators';

  const touchMap = new WeakMap();

  export default {
    name: "DepositMoney",
    data() {
      return {
        amount: '50',
        bonusCode: '',
        pid: '',
        cashierUrl: '',
        isLoading: false
      }
    },
    validations: {
      amount: {
        required
      }
    },
    components: {
      'v-input': Input,
    },
    methods: {
      updateAmount(val) {
        this.delayTouch(this.$v.amount);
        this.amount = val;
      },
      updateBonusCode(val) {
        this.bonusCode = val;
      },
      async deposit() {
        this.$v.amount.$touch();
        if (this.$v.amount.$error) {
          return 0;
        }
        try {
          this.isLoading = true;
          const params = {
            fields: {
              currency: this.currency,
              amount: this.amount,
              bonusCode: this.bonusCode
            }
          };
          const result = await call.userDeposit(params);
          this.pid = result.kwargs.pid;
          this.cashierUrl = result.kwargs.cashierUrl;
        }
        catch (err) {
          const message = err.kwargs.desc || JSON.stringify(err);
          this.$notify({
            group: 'app',
            type: 'error',
            text: message
          });
          console.error(`EM Deposit Error: ${message}`);
        }
        finally {
          this.isLoading = false;
          subscribe.depositStatusChanged(
            (res) => {
              console.log('success');
              console.log(res);
            },
            (res) => {
              console.log('cancel');
              console.log(res);
              this.pid = '';
              this.cashierUrl = '';
            },
            (res) => {
              console.log('error');
              console.log(res);
            },
            {pid: this.pid});
        }
      },
      delayTouch($v) {
        $v.$reset();
        if (touchMap.has($v)) {
          clearTimeout(touchMap.get($v));
        }
        touchMap.set($v, setTimeout($v.$touch, 1000));
      }
    },
    computed: {
      ...mapGetters({
        currency: 'getUserCurrency'
      })
    }
  }
</script>

<style lang="sass" scoped>
  .deposit-money
    min-height: rem(400)
  .checkBoxRow
    justify-content: flex-start

    .checkbox__label
      height: 42px
      width: 90px
      align-items: center
      margin-left: 10px

      &:before
        margin: 0

      &:after
        top: 13px

    .checkbox
      display: flex
      align-items: center
      border: 1px solid #eaeaea
      border-radius: 6px
      margin-right: 20px

      .checkbox__text
        margin: 0 0 0 8px
  iframe
    border: none
</style>
